@import url(https://fonts.googleapis.com/css2?family=Raleway:ital@1&display=swap);
/* parent container로 margin, padding이 튀어나오는 대참사를 막기 위해서 */
* {
  box-sizing: border-box;
}

body {
  background-color: #f0edcc;
}

.input {
  width: 100%;
  padding: 30px;
  font-size: 30pt;
  text-align: center;
  background-color: #02343f;
  border: none;
  outline: none;
  border-radius: 8px;
  color: #f0edcc;
}

.input-container {
  width: 50%;
  margin: 0 auto;
}

.text-container {
  width: 50%;
  margin: 0 auto;
  text-align: center;
}

.container {
  /* 상단에서 약간 간격을 줌 */
  margin-top: 150px;
  /* child element를 모두 중앙정렬 시킴 */
  text-align: center;
}

/* notification이 viewport 상 left에 붙는 걸 막기 위해 */
.text-result-wrapper {
  display: inline-block;
}

.text-result {
  display: inline-block;
  /* ripple effect가 밖으로 튀어나오지 않도록 */
  overflow: hidden;
  /* element가 parent에서 벗어나지 못하게 */
  position: relative;
  font-family: "Raleway", sans-serif;
  margin-top: 50px;
  padding: 30px;
  color: #02343f;
  font-size: 25pt;
}

.notification {
  /* 부드럽게 표시되게 */
  -webkit-animation: 0.5s text-fadein;
          animation: 0.5s text-fadein;
  /* center가 아닌 left에 위치하게 하게 */
  text-align: left;
  font-size: 10pt;
  color: #02343f;
}

.test-dot {
  z-index: 5;
  position: absolute;
  width: 70px;
  height: 70px;
  /* 원으로 만들기 위해서 */
  border-radius: 9999px;
  background-color: #02343f;
  -webkit-animation: 0.9s ease forwards ripple-effect;
          animation: 0.9s ease forwards ripple-effect;
}

@-webkit-keyframes ripple-effect {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
  50% {
    -webkit-transform: scale(10);
            transform: scale(10);
    opacity: 0.5;
  }
  100% {
    -webkit-transform: scale(35);
            transform: scale(35);
    opacity: 0;
  }
}

@keyframes ripple-effect {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
  50% {
    -webkit-transform: scale(10);
            transform: scale(10);
    opacity: 0.5;
  }
  100% {
    -webkit-transform: scale(35);
            transform: scale(35);
    opacity: 0;
  }
}

@-webkit-keyframes text-fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes text-fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.popup-notification {
  /* 다른 element에 가려지지 않게 */
  z-index: 10;
  -webkit-animation: 1s forwards fade-in-out;
          animation: 1s forwards fade-in-out;
  position: absolute;
  /* 중앙정렬 */
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  padding: 32px;
  border-radius: 12px;
  /* 멋진- 그림자 */
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  background-color: white;
}

@-webkit-keyframes fade-in-out {
  0% {
    opacity: 0;
  }
  70% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fade-in-out {
  0% {
    opacity: 0;
  }
  70% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

